.offer {
  text-align: left;

  @media (min-width: @tablet-width) {
    width: 100%;
    margin-top: 40px;
    margin-bottom: 66px;
  }
}

.offer__list {
  width: 320px;
  font-size: 14px;
  line-height: 1.5;
  text-align: left;
  color: @black;
  list-style: none;
  padding-left: 10px;
  box-sizing: border-box;

  @media (min-width: @tablet-width) {
    width: 100%;
    display: flex;
  }
}

.offer__item {
  position: relative;
  box-sizing: border-box;

  @media (min-width: @tablet-width) {
    display: flex;
    flex-direction: column;
    width: 256px;

    &:nth-child(2) {
      order: 1;
    }
  }

  @media (min-width: @desktop-width) {
    width: 400px;
  }
}

.offer__title {
  display: inline-block;
  vertical-align: top;
  font-size: 18px;
  font-weight: bold;
  line-height: 1.44;
  color: @black-three;
  margin-top: 15px;
  margin-left: 49px;
  margin-bottom: 0;

  &--housing {
    &::before {
      content: "";
      background-image: url("../img/icon-house.svg");
      position: absolute;
      object-fit: contain;
      background-size: 33px 38px;
      width: 33px;
      height: 38px;
      left: 0;
      fill: red;
    }
  }

  &--souvenirs {
    &::before {
      content: "";
      background-image: url("../img/icon-gift.svg");
      position: absolute;
      object-fit: contain;
      background-size: 33px 38px;
      width: 33px;
      height: 38px;
      left: 0;
      fill: red;
    }
  }

  &--food {
    &::before {
      content: "";
      background-image: url("../img/icon-burger.svg");
      position: absolute;
      object-fit: contain;
      background-size: 33px 38px;
      width: 33px;
      height: 38px;
      left: 0;
      fill: red;
    }
  }

  @media (min-width: @tablet-width) {
    margin: 0 auto;
    margin-top: 100px;

    &--housing {
      &::before {
        content: "";
        background-size: 75px 72px;
        width: 75px;
        height: 72px;
        top: 10px;
        left: 90px;
        fill: red;
      }
    }

    &--souvenirs {
      &::before {
        content: "";
        background-size: 64px 75px;
        width: 64px;
        height: 75px;
        top: 10px;
        left: 90px;
        fill: red;
      }
    }

    &--food {
      &::before {
        content: "";
        background-size: 77px 68px;
        width: 77px;
        height: 68px;
        top: 10px;
        left: 90px;
        fill: red;
      }
    }
  }

  @media (min-width: @desktop-width) {
    &--housing {
      &::before {
        left: 160px;
      }
    }

    &--souvenirs {
      &::before {
        left: 170px;
      }
    }

    &--food {
      &::before {
        left: 160px;
      }
    }
  }
}

.offer__description {
  display: inline-block;
  width: 210px;
  font-size: 14px;
  font-weight: normal;
  line-height: 1.5;
  color: @black;
  margin-left: 49px;
  margin-top: 0;

  @media (min-width: @tablet-width) {
    text-align: center;
    width: 165px;
    margin: 0 auto;
    margin-top: 10px;
  }
}

.offer__image {
  display: inline-block;
  fill: @light-grey-blue;
  margin-top: 10px;

  &--housing {
    width: 37px;
    height: 36px;
  }

  &--souvenirs {
    width: 32px;
    height: 38px;
  }

  &--food {
    width: 40px;
    height: 35px;
  }
}
